.feedback-form {
  width: 400px;
  background-color: var(--backgorund-color);
  border-radius: 4px;
  flex-direction: column;
  justify-content: center;
  gap: 5px;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  gap: 10px;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  color: var(--button-text);
  font: inherit;
  cursor: pointer;
}

input, textarea {
  background-color: var(--backgorund-color);
  font: inherit;
  height: auto;
  color: var(--button-text);
  border-radius: .5em;
  padding: 5px;
}

textarea {
  resize: none;
}

input {
  border: 1px solid var(--background-color);
}

button {
  cursor: pointer;
  width: 50%;
  height: auto;
  border: 1px solid var(--accent-color);
  background-color: var(--button-color);
  box-shadow: 0px 0px 19px 0px var(--box-shadow);
  border-radius: .6em;
  margin: 0 auto;
  padding: 8px;
}

